$font-size: 16px;

.docs-typography {
    font-size: $font-size;
    line-height: (26px / $font-size);
    position: relative;

    a {
        color: #e8175d;
    }

    .image {
        $local-size: 240px;

        display: block;
        width: $local-size;
        height: floor($local-size * (700 / 1200));
        margin: 12px 0;
        box-shadow: 0 1px 4px rgba(#000, .1),  0 1px 15px rgba(#000, .06);
        cursor: zoom-in;

        img {
            width: inherit;
            height: inherit;
            display: block;
            object-position: top center;
            object-fit: cover;
        }
    }

    .gallery {
        margin: 12px 0;
    }
    .gallery__list {
        display: flex;
        flex-wrap: wrap;
        margin: -8px;
    }
    .gallery__item {
        margin: 8px;

        .image {
            margin: 0;
        }
    }

    code:not(.hljs) {
        padding: 0 4px;
        border-radius: 1.5px;
        background-color: #ebebeb;
        color: #595959;
        word-break: break-word;
    }

    pre {
        code:not(.hljs) {
            display: block;
            font-size: 13px;
            background-color: #ebebeb;
            border-radius: 1.5px;
            color: #595959;
            padding: 4px 10px;
            overflow-x: auto;
        }
    }

    code.hljs {
        background-color: #f5f5f5;
        font-size: 13px;
        border-radius: 1.5px;
        padding: 4px 10px;
    }

    table, td, th {
        border: 1px solid #d9d9d9;
    }

    th {
        background-color: #f2f2f2;
    }

    td, th {
        padding: 6px 14px;
    }
}

@media (max-width: 699px) {
    .docs-typography {
        font-size: 14px;

        ul,
        ol {
            padding-left: 20px;
        }

        a,
        code {
            word-wrap: break-word;
        }
    }
}
