.docs-section {
    margin: 0;
    min-height: 100%;
    box-sizing: border-box;
    padding: 120px 0;
}
.docs-section__title {
    margin: 0 0 50px;
    font-size: 42px;
    font-weight: 500;
}

@media (max-width: 699px) {
    .docs-section {
        padding: 40px 0 20px;
        min-height: auto;

        &:first-child {
            padding-top: 70px;
        }
    }
    .docs-section__title {
        margin: 0 0 30px;
        font-size: 32px;
    }
}
