.docs-nav {
    position: fixed;
    background-color: #333;
    color: #fff;
    width: 220px;
    height: 100%;
    overflow-y: auto;
    z-index: 20;
}
.docs-nav__logo {
    padding: 25px 15px 35px;
}
.docs-nav__links {
    list-style: none;
    margin: 0;
    padding: 0;
}
.docs-nav__link {
    padding: 10px 15px;
    display: block;

    &:hover,
    &--current {
        //background-color: #66ccff;
        //text-shadow: 0 1px 3px rgba(#000, .2);
        background-color: rgba(#fff, .1);
    }
    &--current {
        cursor: default;
    }
}

@media (max-width: 1199px) {
    .docs-nav {
        transform: translateX(-100%);
        transition: transform .3s;
    }
    .docs-nav--open {
        transform: translateX(0);
    }
    .docs-nav-backdrop {
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background: rgba(#999, .95);
        z-index: 15;
        visibility: hidden;
        opacity: 0;
        transition: opacity .3s;
    }
    .docs-nav--open + .docs-nav-backdrop {
        visibility: visible;
        opacity: 1;
    }
}
