/*
// .docs-header
*/


.docs-header {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    height: 42px;
    width: 100%;
    background: #fff;
    box-shadow: 0 1px 3px rgba(#000, .2);
    z-index: 10;
}
.docs-header__menu {
    height: 32px;
    margin: 5px 10px;
    border: none;
    background: transparent;
    font-family: inherit;
    font-size: 15px;
    font-weight: 700;
    padding: 0 10px;
    cursor: pointer;

    &:hover {
        background-color: #f2f2f2;
    }
    &:active {
        background-color: #ebebeb;
    }
    &:focus {
        outline: none;
    }
}

@media (max-width: 1199px) {
    .docs-header {
        display: block;
    }
}
